<template>
  <div class="reset-psw-view view-background">
    <header-view :fixed="fixed" :title="title" :routerName="routerName"></header-view>
    <div class="refund-details">
      <p>订单编号：<span>{{no}}</span></p>
      <p>取消/退款原因：<span>{{reason}}</span></p>
      <p>取消/退款进度：<span class="add-color">{{state}}</span></p>
    </div>
    <ul class="state-list">
      <li v-for="item in stateList">
        <div><span>{{item.step}}</span><span>{{item.date}}</span></div>
      </li>
    </ul>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  import {getrefundDetails} from '../../service/getData'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '取消/退款进度',
        fixed: false,
        routerName: 'orderDetail',
        no: '60958571319',
        reason: '',
        state: '退款处理中',
        stateList: []
      }
    },
    created: function () {
      let _this = this
      getrefundDetails
        .then(function (rep) {
          _this.stateList = rep.data.refundDetails
          _this.no = rep.data.no
          _this.reason = rep.data.reason
          _this.state = rep.data.state
        })
        .catch(function () {
          _this.$toast('获取信息失败')
        })
    }

  }
</script>

<style>
  .refund-details {
    background-color: white;
    padding: .1rem 0.15rem;
  }
  
  .refund-details>p {
    line-height: .25rem;
  }
  
  .refund-details>p>span {
    line-height: .25rem;
    color: #333;
  }
  
  .add-color {
    color: red!important;
  }
  
  .state-list {
    margin-top: .2rem;
    overflow: auto;
    height: 77%;
  }
  
  .state-list>li {
    padding: .1rem 0.15rem;
    background-color: white;
    color: #cacaca;
    border-bottom: .01rem solid #F5F5F5;
  }
  
  .state-list span {
    display: block;
  }
  
  .state-list>li:first-child {
    color: #333;
  }
</style>


